html, body {
  margin: 0;
}

.App {
  text-align: center;
  width: 100vw;
  height: 100vh;
  z-index: -1;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

label {
  cursor: pointer;
}

label:hover {
  font-weight: bold;
}
input[type="file"] {
  z-index: -1;
  font-size: 17px;
  color: #b8b8b8;
  width: 0;
}

#add-song {
  z-index: -1;
  font-size: 17px;
  color: #b8b8b8;
  width: 0;
}

.musicBox {
  position: absolute;
  display: grid;
  justify-content: center;
  align-items: center;
  border: 1px solid black;
  align-content: center;
  width: 130px;
  min-height: 100px;
  border-radius: 5px;
  background-color: #eee;
  z-index: 100;
  cursor: grabbing;
}

.boxWindow {
  background-color: white;
  width: 80%;
  margin: 1em auto;
}

.songName {
  position: relative;
  height: 0;
  margin: auto;
  margin-top: 1em;
  transform: translateX('50px')
}




.closeButton {
  text-align: right;
  cursor: pointer;
}

.closeButton:hover {
  font-weight: bold;
}

.Vol {
  border: 1px solid black;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  line-height: 30px;
  cursor: grabbing;
}